export default {
	data() {
		return {
			payMoney: true,//是否有缴费
			dealPrice:'0.00',
			roomsMaterial:'',
			roomOrderDetail:''
		};
	},
	computed:{
		myRoom(){
		  return this.$store.state.myRoom;
		},
		nowMonth(){
			var date = new Date(); 
			var Y = date.getFullYear();
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
			return Y +''+ M;
		},
	},
	methods: {
		invoice(){
			this.$Router.push({name:'electronic_invoice'})
		},
		toMoreBill(){
			this.$Router.push({name:'more-bill',params:{type:2}})
		},
		pay(url){
			if(!this.dealPrice){
				uni.showToast({
					icon:'none',
					title:'待缴金额为0.00'
				})
				return;
			}
			this.$Router.push({name:url})
		},
		getNav(url){
			if(url==='main/est-pay/pre-pay'){
				uni.navigateTo({
					url: '/pages/'+url+'?data='+JSON.stringify(this.roomsMaterial)
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/'+url
			});
		},
		billTypeDetail(item){
			let datas ={
				status:item.status,
				sn:item.sn,
				allmoney:item.allmoney,
				table_type:item.table_type,
				ymonth:item.ymonth
			}
			this.$Router.push({name:'bill-type-detail',params:{data:datas}})
		},
		toPay(){
			uni.navigateTo({
				url: '/pages/main/est-pay/to-pay'
			});
		},
		getRoomsMaterials(){
			let data ={
				roomid:this.myRoom.roomid
			}
		},
		getNowMonthOrder(){
		}
		
	},
	onLoad() {
		// this.getRoomsMaterials();
		// this.getNowMonthOrder()
	}
};
